<script>
  import {mapState} from 'vuex';
  export default {
    name: "VideoPage",
    components: {
    },
    props: {
      feedbackPageEnabled: {
        type: Boolean,
        default: false
      }
    },
    methods: {
      volumeLow() {
        this.$refs.modalVideo.volume = .3;
      },
      volumeMedium() {
        this.$refs.modalVideo.volume = .6;

      },
      volumeHigh() {
        this.$refs.modalVideo.volume = .9;

      },
      volumeMute() {
        this.$refs.modalVideo.volume = 0;
      },
      // playPause() { 
      //   this.paused = !this.paused;
      //   if (this.$refs.modalVideo.paused) {
      //       this.$refs.modalVideo.play(); 
      //     }
      //   else {
      //       this.$refs.modalVideo.pause(); 
      //   }
      // },
      ended() {
        // console.log(this.$videos, this.$videos.filter(i => i.fields.ID === window.location.href.split("video=")[1])[0].fields.Feedback_Video);
        if (this.feedbackPageEnabled && this.$videos.filter(i => i.fields.ID === window.location.href.split("video=")[1])[0].fields.Feedback_Video) {
          this.$router.push(`/feedback#video=${this.$videos.filter(i => i.fields.ID === window.location.href.split("video=")[1])[0].fields.ID}`);
          this.$store.commit("setActivePage", "FeedbackPage");

          return;
        } else {
            this.goBack();
        }
      },
      goBack() {
        
        document.getElementById("videopage").removeEventListener("click", this.handleClick);
        this.$refs.modalVideo.removeEventListener("ended", this.goBack);

        this.$store.commit("setActivePage", "App");
       
        if (this.homeDirectory.toString() === this.activeDirectory[this.activeDirectory.length - 1].toString()) {

         
          this.$nextTick(() => {
             // Appeases the analytics-collecting
            // this.$router.push("./index.html");

            // Hard refresh to avoid memory leak
            // window.location.href = "./";
            // window.location.href = "./index.html";
            this.$refs.modalVideo.pause();
            delete this.$refs.modalVideo;
            this.$router.push({name: 'app'});

          })

        } else {
          this.$router.push("./#index.html");
        }
      },
      handleClick(e) {

        // Show n hide controls
        this.showControls = true;
        clearTimeout(this.timeout);
        this.timeout = setTimeout(() => {
          this.showControls = false;
          clearTimeout(this.timeout);
        }, 4000);
        
        
        switch(e.target.id) {
          // case "playpause": {
          //   this.playPause();
          //   break;
          // }
          case "mute": {
            this.volumeMute();
            break;
          }
          case "low": {
            this.volumeLow();
            break;
          }
          case "medium": {
            this.volumeMedium();
            break;
          }
          case "high": {
            this.volumeHigh();
            break;
          }
          case "close": {
            this.goBack();
          }
        }
      }
    },
    computed: {
      ...mapState(['carouselTransform', 'carouselPos', 'clickedTilesArray', 'activeDirectory', 'homeDirectory', 'nestedRank'])
    },
    data() {
      return {
        modalVid: null,
        paused: false,
        showControls: false,
        timeout: null,
        timeEnteredOnAnyPage: 0,
        Loop_Video:false
      };
    },
    mounted() {
      this.$store.commit("setActivePage", "VideoPage");
      try {
        // If video found, play video. If not, redirect to homepage
        this.modalVid = this.$videos.filter(i => i.fields.ID === window.location.href.split("video=")[1])[0].fields.Content[0].url;
        } catch {
          window.location.href = "./index.html";
        }
        this.Loop_Video = this.$videos.filter(i => i.fields.ID === window.location.href.split("video=")[1])[0].fields.Loop_Video;
        this.$refs.modalVideo.play();
        document.getElementById("videopage").addEventListener("click", this.handleClick);
        // For now I have to hard code
          this.$refs.modalVideo.addEventListener("ended", this.ended);
    }
  };
</script>

